import * as React from 'react';
import { useState,useContext } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Alert from '@mui/material/Alert';
import TextField from '@mui/material/TextField';
import { Button, CardActionArea, CardActions } from '@mui/material';
import { makeStyles } from '@mui/styles';
import CloudUploadIcon from '@material-ui/icons/CloudUpload'
import './SignUp.css'
import insta from '../assets/Instagram.jpeg'
import {Link} from 'react-router-dom';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { AuthContext } from '../Context/AuthContext';
import { database,storage } from '../firebase';
export default function SignUp() {
    const useStyles = makeStyles({
        text1:{
            color:'grey',
            textAlign:'center'
        },
        card2:{
            height:'5vh',
            marginTop:'1%',

        }
    })
    const classes = useStyles();
    const [email,setEmail] = useState('');
    const [password,setPassword] = useState('')
    const [name,setName] = useState('')
    const [file,setFile] = useState(null);
    const[error,setError] = useState(null);
    const[loading,setLoading] = useState(false);
    const history = useHistory();
    const {signup} = useContext(AuthContext);

    const handleClick = async()=>{
        if(file===null){
            setError("Please upload profile image first");
            setTimeout(()=>{
                setError('')
            },2000)
            return;
        }
        try{
            setError('')
            setLoading(true)
            let userObj = await signup(email,password);
            let uid = userObj.user.uid;
            // console.log(uid);
            const uploadTask = storage.ref(`/users/${uid}/ProfileImage`).put(file);
            uploadTask.on('state_changed',fn1,fn2,fn3)
            function fn1(snapshot){
                let progress = (snapshot.bytesTransferred / snapshot.totalbytes)*100;
                console.log(`Upload is ${progress} done.`)
            }
            function fn2(error){
                setError(error);
                setTimeout(()=>{
                    setError('')
                },2000)
                setLoading(false);
            return;
                // console.log(`error`,error)
            }
            function fn3(error){
                uploadTask.snapshot.ref.getDownloadURL().then((url)=>{
                    // console.log(url);
                    database.users.doc(uid).set({
                        email: email,
                        userId:uid,
                        fullname:name,
                        profileUrl:url,
                        createdAt:database.getTimeStamp(),
                    })
                })
                setLoading(false);
                history.push('/');
            }
        }
        catch(err){
            setError('')
            setError(err);
            setTimeout(()=>{
                setError('')
            },2000)
            return;
        }
    }
    return (
        <div className="signupWrapper">
            <div className="signupcard">

                <Card variant="outlined">
                    <div className='instaLogo'>
                        <img src={insta} alt=""/>
                    </div>
                        <CardContent>
                            <Typography className={classes.text1} variant="subtitle1" >
                                Sign up to see photos and videos from your friends
                            </Typography>
                            {error!=null && <Alert severity="error">{error}</Alert>}
                            <TextField id="outlined-basic" label="email" variant="outlined" fullWidth={true} margin='dense' size='small' value={email} onChange={(e)=>setEmail(e.target.value)}/>
                            <TextField id="outlined-basic" label="password" variant="outlined" fullWidth={true} margin='dense' size='small' value={password} onChange={(e)=>setPassword(e.target.value)}/>
                            <TextField id="outlined-basic" label="Full Name" variant="outlined" fullWidth={true} margin='dense' size='small' value={name} onChange={(e)=>setName(e.target.value)}/>
                            <Button color="secondary" margin="dense" fullWidth={true} startIcon={<CloudUploadIcon/>} component='label'>
                            Upload profile Picture  <input type="file" accept="image/*" hidden onChange={(e)=>setFile(e.target.files[0])}/>
                        </Button>
                        </CardContent>
                    <CardActions>
                        <Button  color="primary" fullWidth={true} variant='contained' disabled = {loading} onClick={handleClick}>
                            Sign Up
                        </Button>
                    </CardActions>
                    <CardContent>
                    <Typography className={classes.text1} variant="subtitle1" >
                    By signing up, you agree to our Terms, Data Policy and Cookie Policy.
                    </Typography>
                    </CardContent>
                </Card>
                <Card variant="outlined" className={classes.card2}>
                <CardContent>
                <Typography className={classes.text1} variant="subtitle1" >
                        Already have an account ?<Link to="/login" style={{textDecoration:"none"}}>Login</Link>
                    </Typography>
                </CardContent>
                </Card>
            </div>
        </div>

    );
}